body {
    font-family: "Muller","sans-serif";
    @include font-size(1.4rem);
    font-style: normal;
    font-weight: normal;
}
button{
    font-family: "Muller","sans-serif";
}

h1,
h2,
h3
 {
    font-family: "MullerBold","sans-serif";
    color: #252525;
}
h4,
h5,
h6 {
    font-family: "MullerMedium", "sans-serif";
    
}

h1 {
    font-size: 7.2rem;
    line-height: 63px;
}

h2 {
    //font-size: 6.4rem;
    line-height: 87%;
     @include font-size(6.4rem);
}

h3 {
    //font-size: 3.6rem;
    line-height: 3.1rem;
     @include font-size(3.6rem);
}

h5 {
    @include font-size(1.6rem);
    line-height: 1.6rem;
}

h4 {
    //font-size: 2.4rem;
    line-height: 2.8rem;
     @include font-size(2.4rem);
}

h6 {
    font-size: 19px;
    line-height: 26px;

}

.subtitle{
    @include font-size(1.8rem);
    line-height: 120%;
    letter-spacing: 0.02em;
    color: $gray1;
    text-align: center;

}

@media(max-width:420px){
    body {
        font-size:1.4rem;
    }
    h2 {
       font-size: 36px;
       line-height: 87%;
    }

    h3 {
       font-size: 18px;
       line-height: 18px;
    }

    h5 {
       font-size: 12px;
       line-height: 12px;
    }

    h4 {
        //font-size: 2.4rem;
        line-height: 2.8rem;
        @include font-size(2.4rem);
    }

    h6 {
        font-size: 19px;
        line-height: 26px;

    }

    .subtitle {
        @include font-size(1.8rem);
        line-height: 120%;
        letter-spacing: 0.02em;
        color: $gray1;
        text-align: center;

    }
}